import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import DiyorTplModal from "../../../../components/DiyorTplModal";

import Logo from "../../../../assets/logo.png";

export default function Header() {
  const navigate = useNavigate();
  const [visible, setVisible] = useState(false);

  const handleClick = (href: string) => navigate(href);

  useEffect(() => {
    const dom = document.querySelector("#resume-header") as Element;
    let now = Date.now();
    let startTop = document.documentElement.scrollTop;

    const fn = () => {
      if (Date.now() - now > 100) {
        let endTop = document.documentElement.scrollTop;
        endTop - startTop > 0
          ? dom?.classList.add(
              "-translate-y-20",
              "transition-transform",
              "duration-1000"
            )
          : dom?.classList.remove("-translate-y-20", "transition-transform");
        now = Date.now();
        startTop = endTop;
      }
    };
    document.addEventListener("scroll", fn);
    return () => {
      document.removeEventListener("scroll", fn);
    };
  }, []);

  return (
    <div className="fixed w-full z-100">
      <header
        id="resume-header"
        className="h-20 bg-white flex justify-between items-center content-center px-10"
      >
        <img
          src={Logo}
          alt=""
          className="h-16 cursor-pointer"
          onClick={() => handleClick("/")}
        />
        <div className="flex items-center w-1/4 justify-between text-lg text-gray-500">
          <span
            onClick={() => handleClick("/tpls")}
            className="cursor-pointer hover:scale-110"
          >
            模板中心
          </span>
          <span
            className="cursor-pointer hover:scale-110"
            onClick={() => handleClick("/interpolation")}
          >
            内推广场
          </span>
          <div className="cursor-pointer hover:scale-110">
            <i className="iconfont icon-bangzhu leading-8 text-xl align-bottom" />
            <span onClick={() => handleClick("/help")}>帮助</span>
          </div>

          <span
            onClick={() => setVisible(true)}
            className="cursor-pointer hover:scale-110 bg-gradient-to-r from-gray-500 to-gray-200 text-white font-bold w-20 p-1 text-center rounded-md"
          >
            开始使用
          </span>
        </div>
      </header>
      <DiyorTplModal visible={visible} setVisible={setVisible} />
    </div>
  );
}
