import { useEffect, useState } from "react";
import { waitImgsLoaded } from "../utils";

export interface Actions {
  setTrue: () => void;
  setFalse: () => void;
  set: (value: boolean) => void;
  toggle: () => void;
}

export default function useImgsLoaded(
  defaultValue = true,
  root: Element = document.querySelector("#root") as Element
): boolean {
  const [loading, setLoading] = useState(defaultValue);

  useEffect(() => {
    async function wait() {
      await waitImgsLoaded(root);
    }
    wait().then(() => {
      setLoading(false);
    });
  }, [root]);

  return loading;
}
