import { Spin } from "antd";
import { useNavigate } from "react-router-dom";
import tpl1 from "../../assets/tpls/tpl1.jpeg";
import tpl2 from "../../assets/tpls/tpl2.jpeg";
import tpl3 from "../../assets/tpls/tpl3.jpeg";
import tpl4 from "../../assets/tpls/tpl4.jpeg";
import tpl5 from "../../assets/tpls/tpl5.jpeg";
import tpl6 from "../../assets/tpls/tpl6.jpeg";
import tpl7 from "../../assets/tpls/tpl7.jpeg";
import tpl8 from "../../assets/tpls/tpl8.jpeg";
import tpl9 from "../../assets/tpls/tpl9.jpeg";
import tpl10 from "../../assets/tpls/tpl10.jpeg";
import tpl11 from "../../assets/tpls/tpl11.jpeg";

import useImgsLoaded from "../../hooks/useImgsLoaded";

import Header from "../header";

const tplArr = [
  tpl1,
  tpl2,
  tpl3,
  tpl4,
  tpl5,
  tpl6,
  tpl7,
  tpl8,
  tpl9,
  tpl10,
  tpl11,
];

export default function Tpls() {
  const navigate = useNavigate();
  const loading = useImgsLoaded();

  const handleToEditor = (i: number | string) => {
    if (i > 7) {
      i = `tpl${i}`;
    }
    navigate(`/editor?${i}`);
  };
  return (
    <>
      <Header />
      <Spin spinning={loading}>
        <div className="bg-gray-100 flex justify-between flex-wrap">
          {tplArr.map((item, i) => (
            <div key={i} className="w-72 h-96 inline-block m-8 relative">
              <div className="w-full h-full absolute bg-gray-500 opacity-0 hover:opacity-80">
                <div
                  className="m-auto mt-40 w-32  h-10 bg-yellow-500 text-center text-white leading-10 text-2xl font-medium rounded-md cursor-pointer"
                  onClick={() => handleToEditor(i + 1)}
                >
                  使用此模板
                </div>
              </div>
              <img className="w-full h-full" src={item} alt="" />
            </div>
          ))}
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
          <i className="w-72  m-8"></i>
        </div>
      </Spin>
    </>
  );
}
