import React, { useEffect, useState } from "react";
import { Button, Dropdown, Menu, Space } from "antd";
import { useLocation, useNavigate } from "react-router-dom";
import { DownOutlined, LeftOutlined } from "@ant-design/icons";
import moment from "moment";

import ToGithub from "../toGithub";
import { Data, BgNodes, Layouts } from "../../index";

import {
  CONSTANTS,
  JSON2JSX_ELEMENT,
  DEFAULT_THEME,
  EXPORT_TYPE,
} from "../../constants";

import print from "../../../../utils/print";
import { handleExportJSON, json2jsx } from "../../../../utils";
import { BGIMG } from "../../../../constants";

interface Props {
  addBlock: ({ type, text, layout }: Data) => void;
  theme: string;
  setTheme: React.Dispatch<React.SetStateAction<string>>;
  layouts: Layouts[];
  setLayouts: React.Dispatch<React.SetStateAction<Layouts[]>>;
  bgNodes: React.CSSProperties[];
  setBgNodes: React.Dispatch<React.SetStateAction<BgNodes[]>>;
  setPrintNodes: React.Dispatch<React.SetStateAction<string[]>>;
  setTotal: React.Dispatch<React.SetStateAction<number>>;
  setPage: React.Dispatch<React.SetStateAction<number>>;
  printNodes: string[];
  page: number;
}

export default function Header({
  layouts,
  theme,
  setTheme,
  setLayouts,
  bgNodes,
  setBgNodes,
  setPrintNodes,
  setTotal,
  setPage,
  printNodes,
  page,
}: Props) {
  const navigate = useNavigate();

  const [toGithubVisible, setToGithubVisible] = useState(false);

  const location = useLocation();

  useEffect(() => {
    if (location.search) {
      handleChangeTpl(location.search.substring(1));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.search]);

  const handleChangeTpl = async (key: string) => {
    let layouts: Layouts[] = [],
      theme: string = DEFAULT_THEME;
    let bgNodes: React.CSSProperties[] = [];

    // 加载tsx文件
    switch (Number(key)) {
      case 1:
        const { layouts1, theme1 } = await import("../../tpl/tpl1");
        layouts = layouts1;
        theme = theme1;
        break;

      case 2:
        const { layouts2, bgNodes2, theme2 } = await import("../../tpl/tpl2");
        layouts = layouts2;
        bgNodes = bgNodes2;
        theme = theme2;
        break;
      case 3:
        const { layouts3, theme3 } = await import("../../tpl/tpl3");
        layouts = layouts3;
        theme = theme3;
        break;
      case 4:
        const { layouts4, bgNodes4, theme4 } = await import("../../tpl/tpl4");
        layouts = layouts4;
        bgNodes = bgNodes4;
        theme = theme4;
        break;
      case 5:
        const { layouts5, bgNodes5, theme5 } = await import("../../tpl/tpl5");
        layouts = layouts5;
        bgNodes = bgNodes5;
        theme = theme5;
        break;
      case 6:
        const { layouts6, bgNodes6, theme6 } = await import("../../tpl/tpl6");
        layouts = layouts6;
        bgNodes = bgNodes6;
        theme = theme6;
        break;
      case 7:
        const { layouts7, bgNodes7, theme7 } = await import("../../tpl/tpl7");
        layouts = layouts7;
        bgNodes = bgNodes7;
        theme = theme7;
        break;
      case 0:
        layouts = [
          {
            block: {},
            layout: [],
          },
        ];
        bgNodes = [];
        break;
      case -1:
        const { layoutsText } = await import("../../tpl/tpl-text");
        layouts = layoutsText;
    }
    // 加载json文件，后续模板都使用json
    if (key.includes("tpl")) {
      const layoutsObj = await import(`../../tpl/${key}.json`);
      layouts = layoutsObj.layouts;
      bgNodes = layoutsObj.bgNodes;
      theme = layoutsObj.theme;
      if (layoutsObj.bgImg) {
        navigate(".", {
          state: {
            bgImg: layoutsObj.bgImg,
          },
        });
      }
    }
    if (!layouts.length) return;
    layouts = layouts.map((item) => {
      Object.keys(item.block).forEach((subItem) => {
        const key = subItem.split("-")[0];

        if (JSON2JSX_ELEMENT.includes(key)) {
          item.block[subItem].text = json2jsx(
            item.block[subItem].text as JSX.Element
          );
        }
      });
      return item;
    });
    setLayouts(layouts);
    setTheme(theme);
    setBgNodes(bgNodes);
    setTotal(layouts.length);
    setPage(0);
  };

  const handleExport = (e: any) => {
    const type = e.key;
    switch (type) {
      case EXPORT_TYPE.PDF:
        const finalPrintArr = [...printNodes];
        finalPrintArr[page] = document.querySelector(
          `#${CONSTANTS.PREVIEW_BOX}${page}`
        )!.innerHTML;

        setPrintNodes(finalPrintArr);

        print(finalPrintArr);
        setToGithubVisible(true);
        break;
      case EXPORT_TYPE.JSON:
        handleExportJSON({
          name: `个人简历-${moment().format("YYYY-MM-DD")}.json`,
          data: {
            layouts,
            theme,
            bgNodes,
            bgImg: (location.state as any)?.[BGIMG],
          },
        });
        break;
    }
  };

  const exportMenu = (
    <Menu
      onClick={handleExport}
      items={[
        {
          label: "PDF",
          key: EXPORT_TYPE.PDF,
        },
        {
          label: "JSON",
          key: EXPORT_TYPE.JSON,
        },
      ]}
    />
  );

  return (
    <header className="fixed w-full bg-white z-10 py-2 px-2 shadow-xl flex justify-between items-center">
      <LeftOutlined className="text-2xl" onClick={() => navigate("/bgs")} />
      <Dropdown overlay={exportMenu}>
        <Button type="dashed">
          <Space>
            导出
            <DownOutlined className="leading-6" />
          </Space>
        </Button>
      </Dropdown>
      <ToGithub visible={toGithubVisible} setVisible={setToGithubVisible} />
    </header>
  );
}
