import { Modal } from "antd";
import React from "react";
import { useNavigate } from "react-router-dom";

import "./index.css";

interface Props {
  visible: boolean;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export default function DiyorTplModal({ visible, setVisible }: Props) {
  const navigate = useNavigate();

  const handleClick = (href: string) => navigate(href);

  return (
    <Modal visible={visible} onCancel={() => setVisible(false)} footer={null}>
      <div className="diy_or_tpl_box">
        <div>
          <div
            className="diy_or_tpl_img diy_or_tpl_img1"
            onClick={() => handleClick("/bgs")}
          ></div>
          <span className="diy_or_tpl_text">自己制作</span>
        </div>
        <div>
          <div
            className="diy_or_tpl_img diy_or_tpl_img2"
            onClick={() => handleClick("/tpls")}
          ></div>
          <span className="diy_or_tpl_text">选择模板</span>
        </div>
      </div>
    </Modal>
  );
}
